import { RoleMappingModel } from "./../../../../../../../shared/models/crud/system/RoleMapping.model";
import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { UserbaseModel, ActionConfig } from "shared/models";
import { UserbaseService } from "shared/services/";
import { getUpdateFilter } from "shared/functions";
import { AlertModel } from "shared/models";
import { AlertService, EnvService } from "shared/services/";

@Component({
  selector: "app-system-system-user",
  templateUrl: "./system-user.component.html",
  styleUrls: ["./system-user.component.scss"]
})
export class SystemUserComponent implements OnInit {
  // @Output() newCreateRecord: EventEmitter<any> = new EventEmitter();
  @Output() mainAnswer: EventEmitter<any> = new EventEmitter();

  // MAIN VARIABLES
  alertMessage: AlertModel; 
  env;
  error_detected = false;
  error_message;

  selection: UserbaseModel;
  action: ActionConfig;

  rows;
  rows_original;
  selected: [{ selected: UserbaseModel }];
  query;

  rowsInTableList;
  rowsInTableSelected;

  show_data;
  show_datatable;
  show_new_button;
  show_selected_button;

  findInList;
  filterFieldList;

  selectedFilterField;

  excel_subtitle;

  // INITIALIZATION
  constructor(
    private userbaseService: UserbaseService,
    private alertService: AlertService,
    private envService: EnvService
  ) {
    this.alertMessage = new AlertModel();

    this.show_data = true;
    this.show_datatable = true;
    this.show_new_button = true;
    this.show_selected_button = false;

    this.selection = new UserbaseModel();

    this.action = new ActionConfig();
    this.excel_subtitle = "usuarios";

    this.selected = [{ selected: new UserbaseModel() }];
    this.rowsInTableList = [
      { id: 1, value: 1 },
      { id: 10, value: 10 },
      { id: 15, value: 15 }
    ];
    this.rowsInTableSelected = { id: 10, value: 10 };
    this.filterFieldList = this.selection.fieldList();
    this.selectedFilterField = {
      field_name: "firstname",
      name: "nombre",
      text: "nombre"
    };
  }

  ngOnInit() {
    // this.onGetActive();
    this.onGetAll();
  }

  onlistAnswer(event) {
    if (event) {
      this.selection = event;
      this.show_selected_button = true;
    }
    event.action === "cancel" ? this.onCancel(event) : "";
    event.action === "afterCreatedRecord"
      ? this.onAfterCreatedRecord(event)
      : "";
    event.action === "afterEditedRecord" ? this.onAfterEditedRecord(event) : "";

    event.action === "editRecord" ? this.onFindById(event) : "";
    event.action === "delete" ? this.onCancel(event) : "";
    event.action === "test" ? this.test(event) : "";
  }

  // IMPORT DATA FROM SERVICE
  getAll_Records(query?) {
    this.userbaseService.getAllRecords(query).subscribe(
      (data: UserbaseModel[]) => {
        let data_mapped = data.map(x => {
          return {
            id: x.id,
            firstname: x.firstname,
            lastname: x.lastname,
            profile: x.profile,
            realm: x.realm,
            username: x.username,
            password: x.password,
            email: x.email,
            emailVerified: x.emailVerified,
            verificationToken: x.verificationToken,
            memberId: x.memberId,
            user_legal_id: x.user_legal_id,
            user_internal_id: x.user_internal_id,
            user_photo_path: x.user_photo_path,
            // Optional
            roles: x.roles,
            roleMapping: x.roles,
            profile_json: x.roles,
            emailVerified_json: { id: 0, name: x.emailVerified }
          };
        });

        this.rows = data_mapped;
        this.rows_original = data_mapped;

        this.alertMessage = new AlertModel();
        this.show_data = true;
      },
      error => {
        this.show_data = false;
        this.onError(error);
      }
    );
  }

  // IMPORT DATA FROM SERVICE
  getSingle_Record(event?) {
    this.rows = [event.temp];
    let id = event.temp.inv_agent_id;

    this.userbaseService.getRecordById(id).subscribe(
      temp => {
        let data = [temp];
        this.rows_original = data;
        this.rows = data;
        this.action.temp = data;

        this.alertMessage = new AlertModel();
        this.show_data = true;
      },
      error => {
        this.show_data = false;
        this.onError(error);
      }
    );
  }

  onGetAll($event?) {
    this.show_datatable = true;
    this.show_selected_button = false;
    this.selection = new UserbaseModel();
    this.query = "";
    this.getAll_Records(this.query);
  }

  /********************************** */

  // BUTTON FOR NEW RECORD
  onNewRecord($event?) {
    this.show_datatable = false;
    this.show_selected_button = false;
    this.action.action = "newRecord";
    // this.action.temp = new UserbaseModel();
    let register = new UserbaseModel();

    localStorage.setItem("recordSelection", JSON.stringify([register]));
  }

  // BUTTON FOR SELECTED
  onEditRecord($event?) {
    this.show_datatable = false;
    this.action.action = "editRecord";
  }

  // SHOW CREATED RECORD
  onAfterCreatedRecord(event?) {
    this.rows = JSON.parse(localStorage.getItem("recordSelection"));
    this.show_datatable = true;
  }

  // SHOW EDITED RECORD
  onAfterEditedRecord(event?) {
    let temp = JSON.parse(localStorage.getItem("recordSelection"));
    this.rows = JSON.parse(localStorage.getItem("recordSelection"));
    this.show_datatable = true;
  }
  /********************************** */

  onShowDetail($event?) {
    this.show_datatable = false;
    this.action.action = "showRecord";
  }

  onItemListSelected(event?) {
    this.show_datatable = true;
    this.rowsInTableSelected = event;
  }

  onFilterFieldList() {
    let temp = {
      lines: this.rowsInTableSelected
    };
    this.action = { action: "rowsInTableSelected", temp: temp };
    this.onGetAll();
  }

  onCancel($event?) {
    if (event) {
      this.show_datatable = !this.show_datatable;
      this.onGetAll();
    }
  }

  onUpdateFilter(event) {
    this.show_datatable = true;
    let temp = getUpdateFilter(
      event,
      this.rows,
      this.rows_original,
      this.selectedFilterField.field_name
    );
    this.rows = temp;
  }

  onFindById(event?) {
    this.getSingle_Record(event);
    this.action.action = "selectedRecord";
    this.show_datatable = true;
  }

  // DETECT EVENTS ON DATATABLE
  onActivate(event) {}

  onSelect(event) {
    this.show_selected_button = true;
    this.selection = event.selected[0];
  }

  // ERROR
  onError(error?) {
    this.alertService.error(error.status);
    this.alertMessage.alertTitle = "Error del servidor";
    this.alertMessage.alertText = error.statusText;
    this.alertMessage.alertShow = true;
    this.alertMessage.alertClass =
      "alert alert-danger alert-dismissible fade show";
  }

  onExportToExcel(data, name) {
    this.show_datatable = true;
    const filterData = data.map(x => {
      return {
        id: x.id,
        nombre: x.firstname,
        apellido: x.lastname,
        perfil: x.profile,
        usuario: x.username,
        email: x.email,
        cedula: x.user_legal_id,
        numero_interno: x.user_internal_id
      };
    });
  }

  test(event) {
    this.rows = event.temp;
    this.show_datatable = true;
  }
}
